






import { debounce, trim } from 'lodash'
import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
const beAudit = namespace('beAudit')
@Component({ components: {} })
export default class FilterAudit extends Vue {
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }
  @beAudit.Action('setReportFilter') setReportFilter!: (data: { contract: string; filters: Record<string, any> }) => void
  @Prop({ default: '', required: false }) search!: string
  valueSearch = this.search
  @Watch('valueSearch', { immediate: false }) handleSearch(keyword: string): void {
    if (keyword !== this.search) {
      this.searchText(keyword)
    }
    if (this.$route?.name) {
      const filters = this.getFilter(this.$route.name)
      if (filters) {
        this.setReportFilter({
          contract: this.$route.name,
          filters: {
            ...filters.filters,
            search: trim(keyword)
          }
        })
      } else {
        this.setReportFilter({
          contract: this.$route.name,
          filters: {
            search: trim(keyword)
          }
        })
      }
    }
  }
  @Watch('$route')
  checkRoute(type: Record<string, any>): void {
    this.init(type)
  }
  init(type: Record<string, any>): void {
    const filters = this.getFilter(type.name)
    if (filters && filters.filters) {
      this.valueSearch = filters.filters.search
    } else {
      this.valueSearch = ''
    }
    // this.searchText(this.valueSearch)
  }
  // created(): void {
  // console.log(this.$route.name)
  // const route = this.$route.name
  // if (route == 'pending' || route == 'refuse' || route == 'lapse') {
  //   this.init(this.$route)
  // }
  // }
  searchText = debounce((value: string) => {
    this.$emit('search', trim(value))
  }, 500)
}
