
































import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import CategoryRepository from '@/services/repositories/category'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class CreateUnit extends Mixins(PopupMixin) {
  @beUser.State('user') user?: Record<string, any>
  @Prop() customerFullName: any
  @Prop() customerCifCode: any
  @Prop() listId: any

  api!: CategoryRepository

  dataInput: any = {
    customerIdNumbers: []
  }
  handleClose(): void {
    this.customerFullName = ''
    this.dataInput.customerIdNumbers = []
    this.setOpenPopup({
      popupName: 'delete-group',
      isOpen: false
    })
  }
  handleOpen(): void {
    console.log('name', this.customerFullName)
    console.log('code', this.customerCifCode)
    console.log('list2', this.listId)
    if (this.listId.length == 0) {
      console.log('k vao')
      this.dataInput.customerIdNumbers.push(this.customerCifCode)
    } else {
      this.dataInput.customerIdNumbers = this.listId
    }
    // this.dataInput.customerIdNumbers = this.list.push('200')
    console.log('input', this.dataInput)
  }
  handleSave(): void {
    this.api
      ?.deleteCustomer(this.dataInput)
      .then(() => {
        this.$message.success('Xóa khách hàng thành công')
        this.$emit('load')
        this.handleClose()
      })
      .catch(er => {
        this.$message.error(er.message)
      })
  }
  created(): void {
    this.api = getRepository('category', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
