





























import PopupMixin from '@/mixins/popup'
import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import getRepository from '@/services'
import CategoryRepository from '@/services/repositories/category'
import UploadRepository from '@/services/repositories/upload'
import { findIndex, filter } from 'lodash'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class CreateUnit extends Mixins(PopupMixin) {
  @beUser.State('user') user?: Record<string, any>
  @Prop() idUpload: any
  @Prop() idUploadDeal: any
  apiUpload!: UploadRepository
  api!: CategoryRepository
  fileList: any = []
  dataInput: any = {
    attachFiles: []
  }
  async handleChangeFile(file: Record<string, any>): Promise<void> {
    const formData = new FormData()
    if (file.status === 'ready') {
      file.type = file.raw.type
      // const newFile: Record<string, any> = {}
      // newFile.name = file.name
      // newFile.size = file.raw.size
      this.fileList.push(file)
      formData.append('files', file.raw)
      // formData.append('name', file.name)
      file.percentage = 1
      const processFunction = function (progressEvent: any) {
        let progress = (progressEvent.loaded / progressEvent.total) * 100
        file.percentage = progress
      }
      const data: Record<string, any> = {}
      data.data = formData
      data.progress = processFunction
      const uid = file.uid
      const result = await this.apiUpload.uploadFile(data)
      const index = findIndex(this.fileList, (item: Record<string, any>) => item.uid === uid)
      this.fileList[index].url = result.success[0].url
    }
  }

  handleDeleteFile(uid: number): void {
    this.fileList = filter(this.fileList, (item: Record<string, any>) => item.uid !== uid)
  }

  handleAddFile(): void {
    if (this.fileList.length) {
      this.dataInput.attachFiles = [...this.fileList]
    }
    if ((this.idUpload && this.$route.name == 'customer') || this.$route.name == 'customerNewsfeed') {
      this.api
        ?.uploadFile(this.idUpload, this.dataInput)
        .then((res: any) => {
          this.$message.success('Upload file thành công')
          this.fileList = []
          this.$emit('load')
          this.handleClose()
        })
        .catch(e => {
          console.log(e)
        })
    } else {
      this.api
        ?.uploadFileDeal(this.idUploadDeal, this.dataInput)
        .then((res: any) => {
          this.$message.success('Upload file thành công')
          this.fileList = []
          this.$emit('load')
          this.handleClose()
        })
        .catch(e => {
          console.log(e)
        })
    }
  }
  handleOpen(): void {
    console.log('test id bên khách hàng', this.idUpload)
    console.log('test id dell', this.idUploadDeal)
    console.log('route', this.$route)
  }
  handleClose(): void {
    this.fileList = []
    // this.idUpload = null
    // this.idUploadDeal = null
    this.setOpenPopup({
      popupName: 'add-upload-file',
      isOpen: false
    })
  }
  created(): void {
    this.apiUpload = getRepository('upload', {
      districtId: this.user?.districts?.[0].id
    })
    this.api = getRepository('category', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
