





















































































































































import PopupAssgin from '../components/popup/PopupAssign.vue'
import PopupMixin from '@/mixins/popup'
import { Component, Mixins, Watch } from 'vue-property-decorator'
import SearchBar from '../../audit/components/filters/Searchbar.vue'
import CreateCustomer from '../components/popup/CreateCustomer.vue'
import FilterMixin from '@/mixins/filterAuditContractMixin'
import AppointCustomer from '../components/popup/AppointCustomer.vue'
import DealRepository from '@/services/repositories/deal'
import { PaginationInterface } from '@/interface'
import { namespace } from 'vuex-class'
import { debounce, filter, trim, forEach } from 'lodash'
import getRepository from '@/services'
import { CUSTOMER_CHANEL } from '@/configs'
import PopupDeleteGroup from '../components/popup/PopupDeleteGroup.vue'
import CreateDeal from '@/modules/deals/components/popup/CreateDeal.vue'
import CustomerFilter from '../components/filter/CustomerFilter.vue'
import PopupConfirmDeal from '@/modules/deals/components/popup/PopupConfirm.vue'
import PopupNote from '../components/popup/PopupNote.vue'
import PopupAddUploadFile from '../components/popup/PopupAddUploadFile.vue'
import { fileExcelAccept } from '@/configs/fileAccept'

const beCustomer = namespace('beCustomer')

@Component({
  components: { SearchBar, CreateCustomer, AppointCustomer, CustomerFilter, PopupConfirmDeal, PopupDeleteGroup, CreateDeal, PopupNote, PopupAddUploadFile, PopupAssgin }
})
export default class customerPage extends Mixins(PopupMixin, FilterMixin) {
  @beCustomer.Action('setDetailIdCustomer') setDetailIdCustomer!: (id: number | string) => void
  isConflickClick = false
  isLoading = false
  apiCustomer!: DealRepository
  data: Record<string, any> = []
  query: PaginationInterface = {
    page: 1,
    limit: 20,
    total: 0
  }
  fileExcelAccept: string[] = fileExcelAccept
  pushData: Record<string, any> = []
  Datas: Record<string, any> = []
  customerFullName = ''
  imageUrl: any = ''
  isUpload: any = false
  customerCifCode = ''
  dataRow: any = {}
  dataPopup: any = {
    title: '',
    code: '',
    id: '',
    data: {}
  }
  dataCustomer: any = {
    id: '',
    customerFullName: '',
    customerCifCode: ''
  }
  filters: Record<string, any> = {}

  type = 'add'
  customerId = 0

  handleUpload(file: Record<string, any>): void {
    // this.$message.success('Đang tải file lên')
    const message = this.$message({
      message: 'Đang tải file lên',
      type: 'success',
      duration: 0
    })
    //@ts-ignore
    this.$message.close()
    const formData = new FormData()
    if (file.status === 'ready') {
      formData.append('file', file.raw)
      formData.append('name', file.name)
      this.apiCustomer
        .importExcelCustomer(formData)
        .then(() => {
          message.close()
          this.$message.success('Tải lên thành công')
          this.init()
        })
        .catch(error => {
          message.close()
          if (error.errorCode === 429) {
            this.$message.error('Không upload được file khi hệ thống đang cập nhật dữ liệu. Vui lòng thử lại sau ít phút')
          } else {
            this.$message.error(error.message)
          }
        })
    }
  }

  loadData(): void {
    this.init()
  }
  listTest: any = []
  // loadFilter(value: Record<string, any>): void {
  //   this.filters = value
  //   this.query = {
  //     ...this.query,
  //     ...this.filters.filters,
  //     sortType: this.filters.sortType
  //   }
  //   this.init()
  // }
  indexMethod(index: number): number {
    return (this.query.page - 1) * this.query.limit + index + 1
  }
  checkShow = false
  listSelect: any = []
  listId = []
  handleSelection(value: Record<string, any>): void {
    this.pushData = value
    this.listSelect = value.map((obj: any) => obj.customerIdNumber)
    this.checkShow = this.listSelect.length > 0 ? true : false
  }
  handleCommand(command: string): void {
    if (command === 'delete') {
      this.listId = this.listSelect
      this.setOpenPopup({
        popupName: 'delete-group',
        isOpen: true
      })
    }
    if (command == 'UpdateNv') {
      this.Datas = this.pushData
      this.setOpenPopup({
        popupName: 'popup-assgin',
        isOpen: true
      })
    }
  }
  init(): void {
    this.isLoading = true
    const temp = {
      ...this.query,
      ...this.filters,
      total: null
    }
    this.apiCustomer
      .getDataCustomer('customers', temp)
      .then((res: any) => {
        this.data = res.content
        this.query.total = res.totalElements
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
  }
  resetCreateCustomer(): void {
    this.init()
  }
  handleReaload(): void {
    this.init()
  }
  handleFiltersChange(filter: Record<string, any>): void {
    ;(this.query.page = 1), (this.query.limit = 20)
    this.filters = {
      ...this.filters,
      ...filter
    }
    this.debounceFilter()
  }
  debounceFilter = debounce(() => {
    this.init()
    // this.getSummary()
  }, 600)

  async handleConfirm(params?: Record<string, any>): Promise<any> {
    if (this.dataPopup.code == 'delete') {
      if (this.dataPopup.id && this.dataPopup.id != 0) {
        await this.apiCustomer
          .deleteDealOrCusById('sales-deals', trim(this.dataPopup.id.toString()))
          .then(() => {
            this.$message.success('Xóa thành công')
            this.setOpenPopup({
              popupName: 'popup-confirm-deal',
              isOpen: false
            })
            this.init()
          })
          .catch(err => this.$message.error(err.message))
      }
    } else {
      await this.apiCustomer
        .updatePersonChangeSale(params)
        .then(() => {
          this.$message.success('Gán thành công')
          this.setOpenPopup({
            popupName: 'popup-confirm-deal',
            isOpen: false
          })
          this.init()
        })
        .catch(err => this.$message.error(err.message))
    }
  }
  Addcharge(params: { status: string; id: number; data?: Record<string, any> }): void {
    const { status, id } = params
    this.dataPopup.code = status
    this.dataPopup.id = id
    if (status == 'assign' || status == 'delete') {
      this.dataPopup.title = status == 'assign' ? 'Gán Nhân viên phụ trách' : 'Xác nhận xóa'
      this.dataPopup.data = params?.data
      this.setOpenPopup({
        popupName: 'popup-confirm-deal',
        isOpen: true
      })
    }
    if (status == 'add' || status == 'edit') {
      this.dataPopup.title = status == 'add' ? 'Thêm mới cơ hội bán hàng' : 'Sửa thông tin cơ hội bán hàng'
      //@ts-ignore
      this.dataCustomer.id = params.data.id
      //@ts-ignore
      this.dataCustomer.customerFullName = params.data.customerFullName
      //@ts-ignore
      this.dataCustomer.customerCifCode = params.data.customerCifCode
      this.setOpenPopup({
        popupName: 'create-deal',
        isOpen: true
      })
    }
  }

  handleEdit(id: any): void {
    this.dataPopup.code = 'edit'
    this.dataPopup.id = id
    this.dataPopup.title = 'Chỉnh sửa khách hàng tiềm năng'
    this.setOpenPopup({
      popupName: 'create-customer',
      isOpen: true
    })
  }
  handleDelete(row: Record<string, any>): void {
    this.customerFullName = row.customerFullName
    this.customerCifCode = row.customerIdNumber
    this.setOpenPopup({
      popupName: 'delete-group',
      isOpen: true
    })
  }
  resetQuery(): void {
    this.query.page = 1
    this.query.limit = 20
  }
  handleSizeChange(size: number): void {
    this.query.limit = size
    this.init()
  }
  handleCurrentChange(page: number): void {
    this.query.page = page
    this.init()
  }
  checkName(item: Record<string, any>): string {
    let nameText = ''
    if (item.customerType == '1') {
      nameText = item.customerFirstName + ' ' + item.customerLastName
    } else {
      nameText = item.customerFullName
    }
    return nameText + '</span>'
  }
  checkStatus(item: Record<string, any>): string {
    let statusText = ''
    if (item.customerStateId == '1') {
      statusText = 'Tiềm năng'
    } else if (item.customerStateId == '2') {
      statusText = 'Đang làm việc'
    } else if (item.customerStateId == '3') {
      statusText = 'Đã ký hợp đồng'
    } else if (item.customerStateId == '4') {
      statusText = 'Đã kết thúc'
    } else if (item.customerStateId == '5') {
      statusText = 'Spam'
    } else if (item.customerStateId == '6') {
      statusText = 'Dupilcated'
    }
    return statusText + '</span>'
  }

  getRelationShip(code: string | any): string {
    return CUSTOMER_CHANEL[code]
  }
  handleConflictClick(): void {
    this.isConflickClick = true
  }
  handleCreateCustomer(): void {
    this.dataPopup.code = 'add'
    this.setOpenPopup({
      popupName: 'create-customer',
      isOpen: true
    })
  }
  idCustomer: string | number = ''
  checkFillCustomer = ''
  handleClickOpenAction(id: string | number, row: Record<string, any>, checkFill: string): void {
    this.idCustomer = id
    this.dataRow = row
    this.checkFillCustomer = checkFill
    this.setOpenPopup({
      popupName: 'appoint-customer',
      isOpen: true
    })
  }
  handleRowClick(row: Record<string, any>): void {
    if (this.isConflickClick) {
      this.isConflickClick = false
      return
    }

    // this.setDetailIdCustomer(row.row.id)
    this.$router.push({
      name: 'customerNewsfeed',
      params: {
        type: this.$route.params.type,
        view: 'bang-tin',
        id: row.row.id
      }
    })
  }
  idUpload: string | number = ''
  handleOpenPopupUpload(id: string | number): void {
    this.idUpload = id
    this.setOpenPopup({
      popupName: 'add-upload-file',
      isOpen: true
    })
  }
  handleCreateNote(post: Record<string, any>): void {
    const type = 'CUSTOMER'
    this.apiCustomer.createNote(type, { description: post.content, customerId: this.customerId }).then(() => {
      this.$message.success('Tạo ghi chú thành công')
      this.init()
    })
  }
  handleOpenPopupNote(row: Record<string, any>): void {
    this.customerId = row.id

    this.setOpenPopup({
      popupName: 'popup-note',
      isOpen: true
    })
  }
  created(): void {
    this.apiCustomer = getRepository('deal', {
      districtId: this.user?.districts?.[0].id
    })
    this.init()
  }
}
