


























import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'
import EmployeeRepository from '@/services/repositories/employee'
import { debounce, trim } from 'lodash'
import getRepository from '@/services'
import DealRepository from '@/services/repositories/deal'

@Component({ components: {} })
export default class PopupAssgin extends Mixins(PopupMixin, DistrictMixin) {
  @Prop() listId: any
  apiDeal!: DealRepository

  dataProp!: Record<string, any>
  apiEmpoly!: EmployeeRepository
  members: any[] = []
  query = {
    members: { i: 0, ps: 20, limit: '', page: 1, search: '', statusAccount: 0, groupCode: 'INSURANCE', sort: 1 }
  }
  salesMemberId = ''
  customerCifCodes = ''
  customerIds = ''

  loadMore(): void {
    this.query.members.i += this.query.members.ps
    this.apiEmpoly.getEmployeeList(this.query.members).then(res => {
      this.members = [...this.members, ...res.data]
    })
  }
  remoteMethod(search: string): void {
    this.query.members.i = 0
    this.query.members.search = search
    this.apiEmpoly.getEmployeeList(this.query.members).then(res => {
      this.members = res.data
    })
  }
  async handleSearch(type: string): Promise<any> {
    switch (type) {
      case 'members':
        await this.apiEmpoly.getEmployeeList(this.query.members).then(res => {
          this.members = res.data
          console.log(this.dataProp?.data.salesMemberId)
          let check = this.members.some(item => item.id == this.dataProp?.data.salesMemberId)
          this.salesMemberId = this.dataProp?.data.salesMemberId ? this.dataProp?.data.salesMemberId : ''
          if (!check && this.salesMemberId) {
            this.members.push({
              id: this.dataProp?.data.salesMemberId,
              fullName: this.dataProp?.data.salesFullName ? this.dataProp?.data.salesFullName : '',
              memberNo: this.dataProp?.data.salesMemberNo ? this.dataProp?.data.salesMemberNo : ''
            })
          }
        })
        break

      default:
        await Promise.all([
          this.apiEmpoly.getEmployeeList(this.query.members).then(res => {
            this.members = res.data
          })
        ])

        break
    }
  }
  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-assgin',
      isOpen: false
    })
  }
  handleConfirm(): void {
    const salesDealIds = this.dataProp.map((item: Record<string, any>) => item.id)
    const customerIdNumber = this.dataProp.map((item: Record<string, any>) => item.customerIdNumber)
    const param = {
      customerIdNumbers: customerIdNumber,
      customerIds: salesDealIds,
      salesMemberId: this.salesMemberId
    }
    this.apiDeal.updatePersonChangeSale(param).then(res => {
      this.$emit('confirm', param)
      this.setOpenPopup({
        popupName: 'popup-assgin',
        isOpen: false
      })
    })
  }
  handleOpen(): void {
    this.dataProp = this.listId
    this.handleSearch('members')
  }
  handleClose(): void {
    this.query.members.search = ''
    this.members = []
    this.salesMemberId = ''
    this.$emit('close')
  }
  created(): void {
    this.apiEmpoly = getRepository('employee', {
      districtId: this.districtId
    })
    this.apiDeal = getRepository('deal', {
      districtId: this.districtId
    })
  }
}
